export default {
    name: 'writer',
    data() {
        return {}
    },
    computed: {
        Tags() {
            if (!this.userTags) {
                return []
            } else {
                return this.userTags.slice(0, 3);
            }

        }
    },
    methods: {
        seeUserDetail(uid) {
            // alert(uid)
            var that = this;
            that.$router.push({
                path: '/writerDetail',
                query: {
                    writerId: that.uid
                }
            })
        }
    },
    mounted() {
    //   console.log(this.sole)
    //   alert(this.imgurl)
      
    },
    props: ['userTags', 'cost','serviceHours',
'serviceOrderCount',
'serviceUserCount','type','tag','authorAuth','feeConfirm', 'uid','sole','joint', 'priceType','avaTaskCount','workHours', 'missionNum', 'hang2IsShow', 'imgurl', 'isMissionDetail', 'isChoosen','shortName', 'workAge', 'realAuth', 'eduAuth', 'userCount', 'isCanChoose']

}



