
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
uid: this.$store.state.uid,
fellowId: ''

}
},
components: {
AlertModule
},
methods: {

toFellow() {
var that = this;
var postData = {
uid: that.uid,
followingUid: that.fellowId
}
if(that.fellowId>=1000000){
AlertModule.show({
title: '没有此用户，请注意输入正确用户ID'
})
return false ;
}
that.$http('post', that.$store.state.baseUrl + 'api/UserFollowing', postData).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
AlertModule.show({
title: "添加成功",
onHide(){
that.goBack()
}
})
}
})
},
goBack() {
this.$router.go(-1)
}
}
}



