
import appFooter from "@/components/footer/appFooter";

import common from "@/assets/js/common";

import slidePicker from "@/components/slidePicker";

import writers from "@/components/writer/writer";

import radioPicker from "@/components/radioPicker";

import sliderPopupPicker from "@/components/sliderPopupPicker";

import sortPopup from "@/components/sortPopup/sortPopup";

import Range from 'vux/src/components/range/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Group from 'vux/src/components/group/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Picker from 'vux/src/components/picker/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'


const pulldownDefaultConfig = {

content: "下拉刷新",

height: 40,

autoRefresh: false,

downContent: "下拉刷新",

upContent: "释放后刷新",

loadingContent: "正在刷新...",

clsPrefix: "xs-plugin-pulldown-"

};

const pullupDefaultConfig = {

content: "上拉加载更多",

pullUpHeight: 60,

height: 40,

autoRefresh: false,

downContent: "释放后加载",

upContent: "上拉加载更多",

loadingContent: "加载中...",

clsPrefix: "xs-plugin-pullup-"

};

export default {



components: {

appFooter,

sortPopup,

Range,

writers,

Popup,

XSwitch,

Cell,

XButton,

Radio,

Checker,

CheckerItem,

Flexbox,

FlexboxItem,

CheckIcon,

GroupTitle,

Group,

Picker,

sliderPopupPicker,

radioPicker,

PopupPicker,

Scroller

},

mounted() {

this.rangValue = 0;



this.getAd()



this.getCategory();

this.getGrade();

this.postData = JSON.parse(this.$route.query.postData);



this.postData.pageSize = 10;

console.log("搜索参数");

console.log(this.postData);

if (this.postData.priceType) {

this.searchPriceType = this.postData.priceType;

}









this.getBigGrade();





this.$nextTick(() => {

this.$refs.scrollerBottom.reset({

top: 0

});

});

this.loadMore();



},



data() {

return {

sortOptions: [{
value: "优先显示我已经加了关注的用户",
key: 'follow'
},
{
value: "优先显示相关家长数最多的用户",
key: 'userCount'
},
{
value: "优先显示家教小时数最多的用户",
key: 'workHours'
},
{
value: "优先显示每人小时费最少的用户",
key: 'price'
},
{
value: "优先显示累计家教值最多的用户",
key: 'work'
}

],

searchPriceType: '',

nowPath: '/index',

classShow: false,

mainShow: true,

categoryShow: false,

sliderClassArr: [],

sliderSubjectArr: [],



gradesArr: [],

leftText1: "年级",

leftText2: "科目",



rightText1: "",

subjectArr: [],

leftText2: "科目",

rightText2: "",

isMissionDetail: true,

isCanChoose: false,



minWorkAge: "",

maxWorkAge: "",

radioTitle: "选择性别",

radios_sex_Arr: [{

value: 0,

text: "不限",

selected: true

},

{

value: 1,

text: "男",

selected: false

},

{

value: 2,

text: "女",

selected: false

}

],

chooseSex: "0",



min: 0,

max: 80,

step: 1,

pxIsShow: false,

sxIsShow: false,

stringValue1: false,

stringValue2: false,

stringValue3: false,

rangValue: 0,



order: "",

postData: {},

writerList: [],

sliderBigClassArr: [],

noDataText: "没有数据了",

page: 1,

allPeople: 0,

allWriter: "",

isBottom: false,

categoryName: "",

list: [],

adList: [], //广告列表

pullupDefaultConfig: pullupDefaultConfig,

pulldownDefaultConfig: pulldownDefaultConfig

};

},

watch: {























},

methods: {

...common,



getAd() {

var that = this;

that

.$http(

"get",

that.$store.state.baseUrl + "api/Ad/List"

)

.then(function(res) {

if (res.data.code != "00") {

AlertModule.show({

title: res.data.msg

});

} else {

that.adList = res.data.data;

for (var i in that.adList) {

that.adList[i].imgurl = that.$store.state.imgUrl + that.adList[i].imgurl

that.adList[i].flag = 'ad'

}



console.log('广告')

console.log(res.data.data);



}

});

},



showSSResult() {



console.log("筛选条件");



var that = this;

that.page = '1';





that.postData = {

realAuth: that.stringValue1 ? '2' : '',

eduAuth: that.stringValue2 ? '2' : '',

authorAuth: that.stringValue3 ? '2' : '',

gender: that.chooseSex,

minWorkAge: that.minWorkAge,

maxWorkAge: that.maxWorkAge,

currentPage: that.page,

loginUid: that.$store.state.uid

};



for (var i in that.postData) {



if (!that.postData[i]) {

delete that.postData[i]

}

}

that.sxIsShow = false;

that

.$http(

"get",

that.$store.state.baseUrl + "api/WebUser/Query/Author",

that.postData

)

.then(function(res) {

if (res.data.code != "00") {

AlertModule.show({

title: res.data.msg

});

} else {



that.writerList = [];

that.loadMore();



}

});

},





getTotalWriter() {



var that = this;

var baseUrl = this.$store.state.baseUrl;

that

.$http("get", baseUrl + "api/WebUser/AuthorTotal")

.then(function(res) {

if (res.data.code != "00") {

AlertModule.show({

title: res.data.msg

});

} else {

that.allWriter = res.data.data;



}

});

},



getMissionOrderType(val) {

var that = this;

that.writerList = [];

console.log("排序" + val)

if (val == '') {

delete that.postData.order

} else {

that.postData.order = val;

}

that.page = 1;

that.postData.currentPage = that.page;

that.postData.pageSize = 10;



that.loadMore()

that.sortOptions = [{

value: "优先显示我已经加了关注的用户",

key: 'follow'

},

{

value: "优先显示相关家长数最多的用户",

key: 'userCount'

},

{

value: "优先显示家教小时数最多的用户",

key: 'workHours'

},

{

value: "优先显示每人小时费最少的用户",

key: 'price'

},

{

value: "优先显示累计家教值最多的用户",

key: 'work'

}



]



},



refresh() {

var that = this;

that.searchResultList(data => {





if (that.postData.currentPage == 1) {



that.$refs.scrollerBottom.donePulldown();

}

that.writerList = data;

that.$refs.scrollerBottom.donePulldown();

});

if (that.postData.currentPage > 0) {

that.postData.currentPage--;

}



that.postData.pageSize = 10;

},

loadMore() {

var that = this;

console.log('执行了loadmore')



that.searchResultList(data => {

if (data.length <= 4) {

that.$refs.scrollerBottom.disablePullup();

}

that.writerList = that.writerList.concat(data);

var index;

for (var i in that.adList) {

index = that.adList[i].adIndex;

that.writerList.splice(index, 1, that.adList[i])

}

console.log('000000000000000000000000000000000000000000')

console.log(that.writerList)

that.$refs.scrollerBottom.donePullup();



});

that.postData.currentPage = that.page;

that.page++;



},







searchResultList(fn) {

var that = this;

that.postData.loginUid = that.$store.state.uid

that.getNameById(that.postData.classNo, that.sliderBigClassArr);

that

.$http(
"get",
that.$store.state.baseUrl + "api/WebUser/Query/Author",
that.postData
)

.then(function(res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg

});

} else {

var result = res.data.data;

for (var i in result) {

result[i].flag = 'writer'

}

that.allPeople = res.data.pi.totalSize;



if (result.length != 0) {



fn(result);

}



}

});



},

getResult() {



var arr = "";

for (let i in this.sliderBigClassArr[0]) {

if (this.sliderBigClassArr[0][i].active == true) {

arr = this.sliderBigClassArr[0][i].name;



}

}



this.mainShow = true;

this.classShow = false;

this.rightText1 = arr;

},

getResult2() {



var arr = "";

for (let i in this.subjectArr) {

if (this.subjectArr[i].active == true) {

arr = this.subjectArr[i].categroy;

this.subject = this.subjectArr[i].id;

}

}

this.mainShow = true;

this.categoryShow = false;

this.rightText2 = arr;

},

gotoMultiPicker(val) {

if (val == "class") {



this.classShow = true;

(this.categoryShow = false),

(this.coordinationShow = false),

(this.mainShow = false);

} else {

(this.classShow = false),

(this.coordinationShow = false),

(this.categoryShow = true);

this.mainShow = false;

}

},



addActive(index) {

for (var i in this.sliderBigClassArr[0]) {

this.sliderBigClassArr[0][i].active = false;

}

this.sliderBigClassArr[0][index].active = true;

},

addActive2(index) {



for (var i in this.subjectArr) {

this.subjectArr[i].active = false;

}

this.subjectArr[index].active = true;

},

changeResult(val) {},

changeResultGrade(val) {



this.postData.classNo = val;

},

changeResultKM(val) {



this.postData.subject = val;

this.categoryName = val.name;

}

},



directives: {

TransferDom

}

};



