
import AlertModule from 'vux/src/plugins/alert/module'
import Badge from 'vux/src/components/badge/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

const pulldownDefaultConfig = {
content: "下拉刷新",
height: 40,
autoRefresh: false,
downContent: "下拉刷新",
upContent: "释放后刷新",
loadingContent: "正在刷新...",
clsPrefix: "xs-plugin-pulldown-"
};
const pullupDefaultConfig = {
content: "上拉加载更多",
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: "释放后加载",
upContent: "上拉加载更多",
loadingContent: "加载中...",
clsPrefix: "xs-plugin-pullup-"
};
import common from "@/assets/js/common";
export default {
data() {
return {
uid: this.$store.state.uid,
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig,
myEwm: "",
userData: {},
myMediator: [],
needMediator: [],
imgArr: [],
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {
x: rect.left,
y: rect.top + pageYScroll,
w: rect.width
}


}
},
};
},
mounted() {
this.getEWM();
this.getWebUser(this.uid);
this.getMediator();







},
components: {
Previewer,
TransferDom,

},
methods: {
...common,

getEWM() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that
.$http("get", baseUrl + "api/WebUser/Qr/" + that.uid)
.then(function (res) {
if (res.data.code != "00") {

AlertModule.show({
title: res.data.msg
});
} else {
that.myEwm = res.data.data;
var obj = {
src: res.data.data
}
that.imgArr.push(obj);
console.log(that.imgArr)
}
});
},

getMediator() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that
.$http("get", baseUrl + "api/Mediator/List", {
uid: that.uid
})
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.myMediator = res.data.data.myMediator;
that.needMediator = res.data.data.needMediator;
}
});
},

addMyMediator() {
this.$router.push({
path: "/myMediator"
});
},
show(i) {
this.$refs.previewer.show(i)
},
}
};



