
import Group from 'vux/src/components/group/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import common from "@/assets/js/common"
import appFooter from '@/components/footer/appFooter'
import cellNav from '@/components/cellNav'

export default {
data() {
return {

nowPath: '/mineIndex',
navList: [{
title: '我发布的任务',
isLink: true,
link: 'myPostMission',
rightNum: 0,
icon: 'icon-ziyuan',
uid: this.$store.state.uid
}, {
title: '我报名的任务',
isLink: true,
link: 'myEnrollMission',
rightNum: 0,
icon: 'icon-liebiao5'
}, {
title: '补充登记',
isLink: true,
link: 'supplementaryRegistration',
rightNum: 0,
icon: 'icon-tianjia'
}, {
title: '预览我的主页',
isLink: true,
link: 'writerDetail',
writerId: this.$store.state.uid,

rightNum: 0,
icon: 'icon-zhuye'
}, {
title: '我的标签',
isLink: true,
link: 'myTags',
rightNum: 0,
icon: 'icon-biaoqian'
},
{
title: '我的关注',
isLink: true,
link: 'myFallow',
rightNum: 0,
icon: 'icon-guanzhu'
},
{
title: '我的信箱',
isLink: true,
link: 'myMailbox',
rightNum: 0,
icon: 'icon-xinfeng'
},
{
title: '系统消息',
isLink: true,
link: 'systemMessage',
rightNum: 0,
icon: 'icon-xiaoxi'
},
{
title: '联系我们',
isLink: true,
link: 'contactUs',
rightNum: 0,
icon: 'icon-dianhua'
},
{
title: '意见反馈',
isLink: true,
link: 'feedback',
rightNum: 0,
icon: 'icon-yijianfankui'
},
{
title: '常见问题',
isLink: true,
link: 'commonProblem',
rightNum: 0,
icon: 'icon-wentiquestions1'
},

{
title: '隐私条款',
isLink: true,
link: 'privacyClause',
rightNum: 0,
icon: 'icon-yinsi'
},
{
title: '合作方登记',
isLink: true,
link: 'partnerRegistration',
rightNum: 0,
icon: 'icon-hezuohuobantianjia'
},
{
title: '关注公众号',
isLink: true,

rightNum: 0,
icon: 'icon-renzhenghuizhang'
}
],
uid: '',
userData: {},
baseObj: {},
persentNum: 0,
systemMsgLength: 0

}
},
components: {
appFooter,
cellNav,
Group,
AlertModule
},

mounted() {
this.getWebUser();
this.getSystemMsg();
this.getNewMailMsg()
},

methods: {
...common,
tofellow(link){
window.open(link)
},

getSystemMsg() {

var that = this;
var baseUrl = this.$store.state.baseUrl;
var arr = []
that
.$http("get", baseUrl + "api/SystemMsg/List", {
uid: that.$store.state.uid
})
.then(function (res) {
console.log(res.data.data);
var result = res.data.data;

for (var i in result) {
console.log(result[i].read == false)
if (result[i].read == false) {
arr.push(result[i])
}
}
that.navList[7].rightNum = arr.length;
});

},

getNewMailMsg() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that
.$http("get", baseUrl + "api/unread/" + that.$store.state.uid, {
type: 'msg'
})
.then(function (res) {
that.navList[6].rightNum = res.data.data
that
.$http("get", baseUrl + "api/unread/" + that.$store.state.uid, {
type: 'dxtask'
})
.then(function (res) {
that.navList[1].rightNum = res.data.data

});
});

},

getWebUser() {
var that = this;
var baseUrl = that.$store.state.baseUrl;
var uid = that.$store.state.uid
that.$http('get', baseUrl + 'api/WebUser/' + uid).then(function (res) {
that.userData = res.data.data;
console.log("用户信息")
console.log(that.userData)

that.navList[0].rightNum = res.data.data.taskCount;

that.navList[1].rightNum = res.data.data.joinCount;

that.navList[4].rightNum = res.data.data.attention

var num = 20;
that.baseObj = {
sex: Boolean(res.data.data.authorInfo.gender),
categories: res.data.data.authorInfo.categories.length == 0 ? false : true,
classList: res.data.data.authorInfo.classList.length == 0 ? false : true,
jointCost: Boolean(res.data.data.authorInfo.jointCost),
soleCost: Boolean(res.data.data.authorInfo.soleCost),
coordination: Boolean(res.data.data.authorInfo.coordination),
area: Boolean(res.data.data.authorInfo.area),
selfCon: Boolean(res.data.data.authorInfo.selfCon)
}

for (var i in that.baseObj) {
if (that.baseObj[i]) {
num += 10;
} else {
num += 0;
}
}
that.persentNum = num;
console.log('基本资料%%%%%%%%%' + num)
console.log(that.baseObj)
})
},

}
}



