
import common from "@/assets/js/common";
import axios from "axios";
import qs from "qs";
import datetimePicker from "@/components/datetimePicker";
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
XButton,
Group,
PopupPicker,
Datetime,
datetimePicker,
AlertModule
},
data() {
return {
mainShow: true,
serviceRecipientShow: false,
finalPrice: 0,
beginDate: "",
endDate: "",
leftText2: "每次时长",
time: [],
timeArr: [],
times: [],
timesArr: [],
leftText1: "阶段次数",

chooseWriterId: this.$route.query.chooseWriterId,

canTalk: false,

taskId: this.$route.query.taskId,
taskObj: {},
stage: 0,
sendSuccess: false,
hasRefuse: false,
hasConfirm: false,
notControl: true,
gapDay: 1,
eachPrice: 0,
timesList: [],
serviceRate: 0,
adjustmentRate: 0,
postUser: {},
confirmList: [],
nicknameArr: [],
talkContent: '',
talkList: [],
infoObj: {},
writerArr: [],
bussArr: [],

serviceReceiver: "",

serviceProvider: "",
missionType: '',
authorId: '',
amount: 0,
isPay: false,
bussNotPay: [],
bussRefuse: [],
bussHasPay: []
};
},

mounted() {
var arr = [];
for (var i = 1; i <= 12; i++) {
arr.push(i);
}
this.timesArr.push(arr);
var arr2 = [];
for (var i = 1; i <= 24; i++) {
arr2.push(i);
}
this.timeArr.push(arr);

this.getMissiondetail(this.taskId);

this.getBaseRate();



this.getTalkList();
this.getStageMsg(this.taskId)
},
methods: {
...common,


goToTalk() {
this.canTalk = true;
},

sendTalkMsg() {
var that = this;
var baseUrl = that.$store.state.baseUrl;
that
.$http("post", baseUrl + "api/TaskTalk", {
uid: that.$store.state.uid,
taskId: that.taskId,
content: that.talkContent
})
.then(function (res) {
that.getTalkList()
that.canTalk = false;

});

},

getTalkList() {
var that = this;
var baseUrl = that.$store.state.baseUrl;
that
.$http("get", baseUrl + "api/TaskTalk/List", {
taskId: that.taskId
})
.then(function (res) {
that.talkList = res.data.data;
console.log("留言列表");
console.log(that.talkList)
});
},


getUser(who, uid) {
var that = this;
var baseUrl = that.$store.state.baseUrl;
var user;
that.$http("get", baseUrl + "api/WebUser/" + uid).then(function (res) {
if (who == "postUser") {
that.postUser = res.data.data;
} else if (who == 'serviceProvider') {
that.serviceProvider = res.data.data;
}
console.log("获取的用户对象");
console.log(that.postUser);
});

},

getConfirmWriter(id) {
var that = this;
var postData = {
taskId: id,
status: "2"
};
that
.$http(
"get",
that.$store.state.baseUrl + "api/Task/Apply/List",
postData
)
.then(function (res) {

if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
var result = res.data.data;
for (var i in result) {
if (result[i].uid == that.taskObj.authorId) {
result.splice(i, 1)
}
}


if (that.taskObj.taskType == 1 || that.taskObj.taskType == 3) {
var obj = {}
obj.user = {}
obj.user.id=that.taskObj.webUser.id;
obj.user.nickname = that.taskObj.webUser.nickname
if (!that.taskObj.payTime) {
obj.pay = 2
} else {
obj.pay = 3
}
result.push(obj)
}


for (var j in result) {
if (result[j].pay == '2') {
result[j].payText="待支付"
that.bussNotPay.push(result[j])
} else if (result[j].pay == '4') {
result[j].payText='已拒绝'
that.bussRefuse.push(result[j])
} else if (result[j].pay == '3') {
result[j].payText='已支付'
that.bussHasPay.push(result[j])
}
}
var nicknameArr = [];
that.bussArr = result;
that.nicknameArr = nicknameArr;
console.log("弹窗需要支付的家长");
console.log(that.bussArr);

}
});

},


getMissiondetail(id) {
var that = this;
that
.$http(
"get",
that.$store.state.baseUrl +
"api/Task/" +
id



)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
that.taskObj = res.data.data;
that.missionType = that.taskObj.taskType;
that.authorId = res.data.data.authorId;
that.getUser("postUser", that.taskObj.uid);
that.getUser("serviceProvider", that.taskObj.authorId);

that.getConfirmWriter(that.taskId);
}
});
},

getStageMsg(id) {
var that = this;
that
.$http(
"get",
that.$store.state.baseUrl +
"api/Task/Stage/" +
id



)
.then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {

if (Boolean(res.data.data)) {
var result = res.data.data
result.beginDate = result.beginDate.substr(0, 10);
result.endDate = result.endDate.substr(0, 10);
that.infoObj = result;
console.log('阶段信息')

console.log(res.data.data)
}

}
});
},

sendRefuse() {
var that = this;
var postData = {
taskId: that.taskId,
status: "3",
uid: that.$store.state.uid
};
that
.$http(
"post",
that.$store.state.baseUrl +
"api/Task/Stage/Confirm",
postData
).then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
AlertModule.show({
title: "拒绝成功",
onHide() {
that.sendSuccess = true;
that.$router.push({
path: "/mission",
});
}
});
that.notControl = false;
that.hasRefuse = true;
that.hasConfirm = false;

}
});

},

sendConfirm() {
var that = this;
var postData = {
taskId: that.taskId,
status: "2",
uid: that.$store.state.uid
};
that
.$http(
"post",
that.$store.state.baseUrl +
"api/Task/Stage/Confirm",
postData
).then(function (res) {
if (res.data.code != "00") {
AlertModule.show({
title: res.data.msg
});
} else {
AlertModule.show({
title: "确认成功",
onHide() {

that.sendSuccess = true;
that.$router.push({
path: "/mission",
});

}
});
that.notControl = false;
that.hasRefuse = false;
that.hasConfirm = true;

}
});

},


timesChange(val) {

this.stage = val.join("");
},

timeChange(val) {},

beginDateChange(val) {
this.beginDate = val;
},

endDateChange(val) {
this.endDate = val;
}
}
};



