
import sliderPopupPicker from "@/components/sliderPopupPicker";
import radioPicker from "@/components/radioPicker";
import datetimePicker from "@/components/datetimePicker";
import multiPicker from "@/components/multiPicker";
import common from "@/assets/js/common";
import axios from 'axios'
import Range from 'vux/src/components/range/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Alert from 'vux/src/components/alert/index.vue'
import value2name from 'vux/src/filters/value2name.js'


export default {
name: "myMainPage",
components: {
datetimePicker,
XTextarea,
Range,
Popup,
XSwitch,
Cell,
XButton,
Group,
Picker,
sliderPopupPicker,
radioPicker,
ChinaAddressV4Data,
XAddress,
AlertModule,
Alert,
multiPicker
},
data() {
return {

myImage: '',
classNo: [],
subject: [],
soleCost: "",
jointCost: "",
category: "",
taskType: "", //组班方式
priceType: "",
coordinations: [], //上门方式
area: "",
introductionContent: "",
content: "",
priceMin: "",
priceMax: "",
isios: false,

multiResult: {},
gradesArr: [],
leftText1: "年级",
rightText1: "不限",
rightText2: "不限",
rightText3: "不限",
nickName1: "className",
nickName2: "categroy",
classShow: false,
categoryShow: false,
coordinationShow: false,
mainShow: true,
gender: "1",
subjectArr: [],
priceArr: [],
slidePriceArr2: [],
coordination: [{
name: "家教拜访",
active: false
},
{
name: "家长拜访",
active: false
},
{
name: "远程协作",
active: false
}
],
userData: {},
booksellerArr: [
[1, 2, 3, 4, 5]
],
leftText2: "科目",
leftText3: "1对1费用",
leftText4: "上门方式",
leftText5: "上门区域",
leftText6: "招标家长人数",
leftText7: "1对多费用",
introduction: "",
content: "",


radioTitle: "选择性别",
publish_title: "性别",
radiosArr: [{
value: 1,
text: "男",
selected: true
},
{
value: 2,
text: "女",
selected: false
}
],

addressData: [{"name":"上海市","value":"310000"},{"name":"市辖区","value":"310100","parent":"310000"},{"name":"黄浦区","value":"310101","parent":"310100"},{"name":"徐汇区","value":"310104","parent":"310100"},{"name":"长宁区","value":"310105","parent":"310100"},{"name":"静安区","value":"310106","parent":"310100"},{"name":"普陀区","value":"310107","parent":"310100"},{"name":"虹口区","value":"310109","parent":"310100"},{"name":"杨浦区","value":"310110","parent":"310100"},{"name":"闵行区","value":"310112","parent":"310100"},{"name":"宝山区","value":"310113","parent":"310100"},{"name":"嘉定区","value":"310114","parent":"310100"},{"name":"浦东新区","value":"310115","parent":"310100"},{"name":"金山区","value":"310116","parent":"310100"},{"name":"松江区","value":"310117","parent":"310100"},{"name":"青浦区","value":"310118","parent":"310100"},{"name":"奉贤区","value":"310120","parent":"310100"},{"name":"崇明区","value":"310151","parent":"310100"}],
date: "",
postData: {},
uid: this.$store.state.uid,
rightText4: "不限",
rightText5: "不限",
address:  [],
editFlag: false,
notEditshortName: '',
shortName: '',
myImage: ''
};
},
mounted() {
this.getCategory();
this.getGrade();
this.getWebUser();
this.getPriceArr();


var ua = navigator.userAgent.toLowerCase();

var isIos = (ua.indexOf('iphone') != -1) || (ua.indexOf('ipad') != -1);

if (isIos) {
this.isios = true;
} else {
this.isios = false
}
},

methods: {
...common,
chooseImg() {
var file1 = document.getElementById("upload_file");
file1.click()
},

chooseImage() {
var that = this;
var baseUrl = that.$store.state.baseUrl
var file = document.getElementById("upload_file").files[0];
var formdata1 = new FormData(); 
formdata1.append('img', file); 
let config = {
headers: {
'Content-Type': 'multipart/form-data'
}
};

axios.post(baseUrl + 'file/img', formdata1, config).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

var showUrl = res.data.data
that.myImage = showUrl;

console.log('我的头像')
console.log(that.myImage)
}
})
},

getWebUser() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that
.$http("get", baseUrl + "api/WebUser/" + this.uid)
.then(function (res) {
that.userData = res.data.data;
that.myImage = res.data.data.imgurl

var grade = res.data.data.authorInfo.classParent.split(",");
var gradeArr = [];
var subject = res.data.data.authorInfo.subject.split(",");
var subjectArr = [];
var firstName = res.data.data.realName.firstName;
var type = res.data.data.type == "author" ? "家教" : "家长";
for (var i in grade) {
gradeArr.push(grade[i].substr(0, 2));
}
grade = gradeArr.join("");
for (var i in subject) {
subjectArr.push(subject[i].substr(0, 2));
}
subject = subjectArr.join("");


that.shortName = res.data.data.nickname;
that.notEditshortName = grade + subject + firstName + type;






if (res.data.data.authorInfo.gender == '1') {
that.radiosArr[0].selected = true;
that.radiosArr[1].selected = false;
that.gender = '1'
} else {
that.radiosArr[1].selected = true;
that.radiosArr[0].selected = false;
that.gender = '2'
}


that.rightText1 = res.data.data.authorInfo.classNo;

var clsNoArr = []
for (var i in res.data.data.authorInfo.classList) {
clsNoArr.push(res.data.data.authorInfo.classList[i].classId)
}
that.classNo = clsNoArr

var cateNoArr = []
for (var i in res.data.data.authorInfo.categories) {
cateNoArr.push(res.data.data.authorInfo.categories[i].categoryId)
}
that.subject = cateNoArr


that.rightText2 = res.data.data.authorInfo.subject;


that.rightText4 = res.data.data.authorInfo.soleCost + "元";
that.soleCost = res.data.data.authorInfo.soleCost
.split("元")[0]
.split("~")
.join("-");

that.rightText5 = res.data.data.authorInfo.jointCost + "元";

that.jointCost = res.data.data.authorInfo.jointCost
.split("元")[0]
.split("~")
.join("-");

that.coordinations = res.data.data.authorInfo.coordination;
that.rightText3 = res.data.data.authorInfo.coordination;
var coorditaArr = that.rightText3.split(',');

for (var i in coorditaArr) {
for (var j in that.coordination) {
if (coorditaArr[i] == that.coordination[j].name) {
that.coordination[j].active = true;
}
}

}

that.address = res.data.data.authorInfo.area.split(",");
that.area = res.data.data.authorInfo.area.split(",");

that.introductionContent = res.data.data.authorInfo.selfCon;
});
},

addActive(index) {
this.gradesArr[index].active = !this.gradesArr[index].active;
},
addActive2(index) {
this.subjectArr[index].active = !this.subjectArr[index].active;
},
addActive3(index) {
this.coordination[index].active = !this.coordination[index].active;
},

getResult() {
this.classNo = []

var arr = [];
console.log(this.gradesArr)
console.log(this.gradesArr)
for (let i in this.gradesArr) {
if (this.gradesArr[i].active == true) {
arr.push(this.gradesArr[i].className);
this.classNo.push(this.gradesArr[i].id);
}
}
this.mainShow = true;
this.classShow = false;
this.rightText1 = arr.join(",");
console.log('新获取的年级')
console.log(this.classNo)
},
getResult2() {
this.subject = []

var arr = [];
for (let i in this.subjectArr) {
if (this.subjectArr[i].active == true) {
arr.push(this.subjectArr[i].categroy);
this.subject.push(this.subjectArr[i].id);
}
}
this.mainShow = true;
this.categoryShow = false;
this.rightText2 = arr.join(",");
},
getResult3() {

var arr = [];
for (let i in this.coordination) {
if (this.coordination[i].active == true) {
arr.push(this.coordination[i].name);
}
}
this.coordinations = arr;
this.mainShow = true;
this.coordinationShow = false;
this.rightText3 = arr.join(",");

},

getGender(val) {
this.gender = val;
},

onAddressChange() {

this.area = value2name(this.address, ChinaAddressV4Data).split(' ').join(",");
console.log(this.area)
},
gotoMultiPicker(val) {
if (val == "class") {

this.classShow = true;
(this.categoryShow = false),
(this.coordinationShow = false),
(this.mainShow = false);
} else if (val == "coordination") {
this.coordinationShow = true;
(this.classShow = false),
(this.categoryShow = false),
(this.mainShow = false);
} else {
(this.classShow = false),
(this.coordinationShow = false),
(this.categoryShow = true);
this.mainShow = false;
}
},
dateChange(value) {
console.log(value);
this.postData.deadline = value;
},
changeResultGrade(val) {

this.postData.classNo = val;
console.log('年纪' + val)
},
changeResultKM(val) {

this.postData.category = val;
},
changeResultSoleCost(val) {

this.rightText4 = val.name
this.soleCost = val.name
.split("元")[0]
.split("~")
.join("-");
console.log("1对1费用");
console.log(this.rightText4)
},
changeResultJointCost(val) {
this.rightText5 = val.name
this.jointCost = val.name
.split("元")[0]
.split("~")
.join("-");
},
shortNamePut() {
this.editFlag = true;
console.log(this.editFlag)
},

savuAllContent() {
var that = this;
console.log(typeof that.coordinations)
if (that.coordinations instanceof Array) {
that.coordinations = that.coordinations.join(",")
}

var postData = {
uid: that.uid,
gender: that.gender,
classNo: that.classNo.indexOf(',') > 0 ? that.classNojoin(",") : that.classNo,
subject: that.subject.indexOf(',') > 0 ? that.subject.join(",") : that.subject,
soleCost: that.soleCost, //1对1费用
jointCost: that.jointCost, //1对多费用
coordination: that.coordinations,
area: that.area,
selfCon: that.introductionContent,
nickname: that.shortName


};

var flag = false;
for (var i in postData) {
if (postData[i] == '') {
flag = false;
AlertModule.show({
title: "请填写全部信息"
});
break;
} else {
flag = true;
continue
}
}
console.log(postData)

if (flag) {
that
.$http(
"post",
that.$store.state.baseUrl + "api/WebUser/Author",
postData
)
.then(function (res) {
var id = that.$store.state.uid;
var data = {
imgurl: that.myImage,
id: id
}
var url = that.$store.state.baseUrl + '/api/WebUser/' + id;

that.$http('put', url, data).then(function (res) {

if (res.data.code == '00') {

if (res.data.code == "00") {
AlertModule.show({
title: "保存成功！",
onHide() {
that.$router.push({
path: '/mineIndex'
})
}
});
that.getWebUser()
} else {
AlertModule.show({
title: res.data.msg
});
}
}
})

});
}
}
}
};



