
import common from "@/assets/js/common"
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
content: '',
tags:[],
goodtags: [],
midtags: [],
badtags: [],

}
},
components: {
XTextarea,
AlertModule
},
mounted() {
this.getTasgs()
this.sortingTags()
},
methods: {
...common,
sortingTags(){
















},

chooseTagsG(i) {
this.goodtags[i].active = true;
},
chooseTagsB(i) {
this.badtags[i].active = true;
},
saveMyTags() {

var that = this;
var baseUrl = this.$store.state.baseUrl;
var arr = []
for (var i in this.goodtags) {
if (this.goodtags[i].active == true) {
arr.push(this.goodtags[i].tagName)
}
}
for (var i in this.badtags) {
if (this.badtags[i].active == true) {
arr.push(this.badtags[i].tagName)
}
}
var postData = {
uid: this.$store.state.uid,
taskId: this.$route.query.taskId,
content: this.content,
commentTags: arr.join(',')
}
that.$http('post', baseUrl + 'api/TaskComment', postData).then(function (res) {
if (res.data.code == '00') {
AlertModule.show({
title: '提交成功',
onHide(){
that.$router.push({
path:'/mineIndex',
query:{
keepActive:true,
id: that.$route.query.taskId
}
})
}
})
} else {
AlertModule.show({
title: res.data.msg
})
}
})
}
}
}



