
import common from "@/assets/js/common";
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {

postList: [],
postData: {},
tabList: [{
name: "全部",
active: true,
id: ''
},
{
name: "待确认",
active: false,
id: 1
},
{
name: "执行中",
active: false,
id: 8
},
{
name: "已结束",
active: false,
id: 4
}
],
isCancel: false
};
},
components: {
AlertModule
},
mounted() {

this.chooseTab(0)
},
methods: {
...common,

toSeemyPostMissionDetail(i) {
var that = this;
var id = that.postList[i].id;
var status = that.postList[i].status;
var autherId = that.postList[i].authorId;
var isAuthor = false;;
if (autherId == that.$store.state.uid) {
isAuthor = true;
}

var tabId;
for (var i in that.tabList) {
if (that.tabList[i].active) {
tabId = that.tabList[i].id;
}
}



if (status == '执行中' || status == '双方已确认代付款') {
if (isAuthor) {


that.$router.push({
path: "/missionInExecutionAuth",
query: {
id: id
}
});
} else {
that.$http('get', that.$store.state.baseUrl + 'api/Task/user-apply?taskId=' + id + "&uid=" + that.$store.state.uid).then(function (res) {

if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {

if (!res.data.data) {
AlertModule.show({
title: "抱歉，任务已在执行中，而您未被选中"
})
} else {
if (res.data.data.pay == '2') {
that.$router.push({
path: "/replyDetailsToPay",
query: {
taskId: id

}
});
} else {
that.$router.push({
path: "/missionInExecutionBuss",
query: {
id: id
}
});
}

}























}
})

}

} else if (status == "待确认") {
this.$router.push({
path: "/toConfirmMission",
query: {
taskId: id,
isMyTask: false

}
});
} else if (status == '已选定服务人' || status == '已有人报名') {
that.$router.push({
path: "/missionDetail",
query: {
id: id,
isMyTask: false
}
});
} else if (status == '双方已确认代付款') {
if (isAuthor) {
that.$router.push({
path: "/missionInExecutionAuth",
query: {
id: id
}
});
} else {
this.$router.push({
path: "/replyDetailsToPay",
query: {
taskId: id

}
});
}
} else {
if (isAuthor) {
that.$router.push({
path: "/missionInExecutionAuth",
query: {
id: id
}
});
} else {
this.$router.push({
path: "/missionInExecutionBuss",
query: {
id: id,
isCancel: true
}
});
}
}
},

chooseTab(index) {
for (let i in this.tabList) {
this.tabList[i].active = false;
}
this.tabList[index].active = true;
var status = this.tabList[index].id;
this.getMIssionlist(status);
},

getMIssionlist(status) {
var that = this;
that.postData.uid = this.$store.state.uid;

that.postData.status = status;

console.log(that.postData);
that
.$http(
"get",
that.$store.state.baseUrl + "api/Task/MyApply/Task",
that.postData
)
.then(function (res) {

if (res.data.code == "00") {
var result = res.data.data;

for (var i in result) {
var arr = result[i].priceRange.split("-");
result[i].priceMin = arr[0];
result[i].priceMax = arr[1];

if (result[i].taskType == 1) {
result[i].borderColor = "zz";
} else if (result[i].taskType == 2) {
result[i].borderColor = "ss";
} else {
result[i].borderColor = "sz";
}
if (result[i].webUser != null) {
let s = that.shortName(
result[i].webUser.authorInfo.classParent
);
let c = that.shortName(result[i].webUser.authorInfo.subject);
result[i].shortClassName = s;
result[i].shortCategory = c;
}
switch (result[i].status) {
case 1:
result[i].status = '发布中'
break;
case 2:
result[i].status = '已选定服务人'
break;
case 3:
result[i].status = '双方已确认代付款'
break;
case 4:
that.isCancel = true;
result[i].status = '已结束'
break;
case 6:
result[i].status = '已取消'
break;
case 7:
result[i].status = '已有人报名'
break;
case 8:
result[i].status = '执行中'
break;
case 88:
result[i].status = '取消中'
break;
case 99:
result[i].status = '已失败'
break;
}
}

that.postList = result;
console.log("我报名的任务");
console.log(res.data.data);
} else {



}
});
}
}
};



