
import AlertModule from 'vux/src/plugins/alert/module'

export default {
data() {
return {
isSaw: false,
mailId: this.$route.query.mailId,
mailDetail: {},
comments: [],
msgId: '',
content: ''
}
},
components: {
AlertModule
},
mounted() {
this.getDetail();
this.$refs.input.focus()
},
methods: {
getDetail() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('get', baseUrl + 'api/PrivateMsg/' + that.mailId).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.mailDetail = res.data.data;
that.comments = res.data.data.comments;

that.$http('put', baseUrl + 'api/PrivateMsg/read/' + that.mailId).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {


}
})

}
})
},

replayMsgId(id) {
this.msgId = id;
},
sendMsg() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
that.$http('post', baseUrl + 'api/PrivateMsg/comment', {
privateMsgId: that.mailDetail.id,
uid: that.$store.state.uid,
content: that.content
}).then(function (res) {
if (res.data.code != '00') {
AlertModule.show({
title: res.data.msg
})
} else {
that.getDetail();
that.content=''
}
})

}
}
}



