
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Toast from 'vux/src/components/toast/index.vue'

import { setTimeout } from 'timers';

export default {
data() {
return {
uid: this.$store.state.uid,
nickname: '',
mobile: '',
content: '',
showToast:false
}

},
components: {
XTextarea,
AlertModule,
Toast
},
methods: {
postFeedback() {
var that = this;
var baseUrl = this.$store.state.baseUrl;
var postData = {
uid: this.uid,
nickname: this.nickname,
mobile: this.mobile,
content: this.content
}
if (this.content) {
that.$http('post', baseUrl + 'api/FeedBack', postData).then(function (res) {
console.log("提交结果")
console.log(res.data)
if (res.data.code == '00') {
that.showToast=true;

setTimeout(()=>{
that.$router.go(-1)
},1000)
} else {
AlertModule.show({
title: res.data.msg
})
}
})
} else {
AlertModule.show({
title: "请输入反馈意见"
})
}

}
}
}



