nowPath: "/mission",

borderColor: "",

aLLIsShow: false,

pxIsShow: false,

sxIsShow: false,

typeShow: false,

rangValue: 0,

sliderSubjectArr: [],

sliderBigClassArr: [],

sliderClassArr: [],

slidePriceArr: [],

slidePriceArr2: [],



subjectArr: [],

gradesArr: [],

leftText1: "年级",

leftText2: "科目",

rightText1: "不限",

rightText4: "",

rightText3: "",

leftText3: "每小时费用",

leftText4: "上门方式",

leftText5: "上门区域",

myarea: [],

coordination: [

["不限", "家教拜访", "家长拜访", "远程协作"]

],

coordination2: [

["不限", "家教拜访", "家长拜访", "远程协作"]

],

rightText2: "不限",



radioTitle: "选择性别",

publish_title: "组班方式",

publish_Arr: [{

