}

],

radios_sex_Arr: [{

value: 0,

text: "不限",

selected: true

},

{

value: 1,

text: "男",

selected: false

},

{

value: 2,

text: "女",

selected: false

}

],

minWorkAge: "",

maxWorkAge: "",



step: 1,

pxIsShow: false,

stringValue1: false,

stringValue2: false,

stringValue3: false,



postList: [],



postData: {},

priceMin: "",

priceMax: "",

publish_title: "组班方式",

radiosArr: [{

value: 1,

text: "1对1",

selected: true

},

{

value: 2,

text: "1对多",

selected: false

}

],

SSdata: {},

nowPage: 1,

isBottom: false,

onFetching: false,

classShow: false,

categoryShow: false,

coordinationShow: false,

mainShow: true,

allWriter: "",

list: [],

userData: {},

missionTypeText: "全部类型",

